}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: black;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  color: black;
  opacity: 0.75;
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

h1 {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 36.65248px;
  letter-spacing: 0.05em;
}

h3 {
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  font-size: 22.65248px;
  letter-spacing: 0.05em;
}

p {
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 300;
  font-size: 14px;
  letter-spacing: 0.08em;
}

a {
  text-decoration: none;
}

.footer {
  width: 100%;
  background: #0a0a0a;
  padding: 30px 30px 20px 30px;
  text-align: center;
}

.footer_sign {
  width: 100px;
  margin: 0 auto;
}

.footer_sign svg {
  width: 100px;
  height: 100px;
  border: 3px solid #5b622e;
  padding: 10px;
  fill: #5b622e;
}

.footer_contact {
  margin: 20px 0;
}

.footer_contact .i-mail {
  color: #5b622e;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 132px;
}

.footer_contact .i-mail svg {
  width: 45px;
  height: 30px;
  fill: #5b622e;
  padding-right: 5px;
  transition: all 300ms;
}

.footer_contact .i-mail p {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  transition: all 300ms;
}

.footer_contact .i-mail:hover {
  color: #9ba74f;
}

.footer_contact .i-mail:hover svg {
  fill: #9ba74f;
}

.footer_bd {
  min-width: 200px;
  max-width: 250px;
  height: 1px;
  background: #5b622e;
  margin: 0 auto;
}

.footer_links {
  margin: 20px 0;
  display: flex;
  justify-content: center;
}

.footer_links a {
  position: relative;
  margin: 0 5px;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: 1px solid #5b622e;
  transition: all 300ms;
}

.footer_links a .iStyle,
.footer_links a i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  transition: all 300ms;
}

.footer_links a .iStyle {
  width: 20px;
  height: 20px;
  fill: #5b622e;
}

.footer_links a i {
  font-size: 20px;
  color: #5b622e;
}

.footer_links a:hover {
  border: 1px solid #9ba74f;
}

.footer_links a:hover .iStyle,
.footer_links a:hover i {
  fill: #9ba74f;
  color: #9ba74f;
}

.footer .small {
  margin-top: 25px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  color: #5b622e;
  font-size: 14px;
}

.glHeader {
  height: 60px;
}

.glHeader_hmg {
  z-index: 100;
  position: fixed;
  top: 30px;
  transform: translateY(-50%);
  right: 15px;
}

.glHeader_hmg p {
  display: none;
}

.glHeader_hmg_btn {
  position: relative;
  cursor: pointer;
  width: 25px;
  height: 25px;
}

.glHeader_hmg_btn span {
  position: absolute;
  width: 25px;
  height: 2px;
  background: #fff;
  transition: all 500ms;
}

.glHeader_hmg_btn span:nth-child(1) {
  top: 30%;
}

.glHeader_hmg_btn span:nth-child(2) {
  bottom: 30%;
}

.glHeader_nav {
  position: fixed;
  visibility: hidden;
  bottom: 0;
  padding-left: 20px;
  right: 0px;
  width: 100%;
  min-height: 320px;
  background: #0a0a0a;
  opacity: 0;
  transition: all 600ms;
  z-index: 20;
}

.glHeader_nav ul {
  margin: 0;
  padding: 70px 0 40px 0;
  position: relative;
  height: 100%;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}

.glHeader_nav li {
  position: relative;
  right: -300px;
  font-size: 22.65248px;
  font-family: "Montserrat", sans-serif;
  letter-spacing: normal;
  padding: 0 30px;
  text-align: right;
  font-weight: 500;
  transition: transform 0.4s ease, opacity 0.2s ease;
  font-size: 20px;
  letter-spacing: 0.2em;
}

.glHeader_nav li:nth-child(2) {
  transition-delay: 0.1s;
}

.glHeader_nav li:nth-child(3) {
  transition-delay: 0.2s;
}

.glHeader_nav li:nth-child(4) {
  transition-delay: 0.3s;
}

.glHeader_nav li:nth-child(5) {
  transition-delay: 0.4s;
}

.glHeader_nav li:nth-child(6) {
  transition-delay: 0.5s;
}

.glHeader_nav li a {
  color: #fff;
}

.glHeader_nav_text {
  display: none;
}

.glHeader_nav_links {
  position: absolute;
  bottom: 20px;
  left: 20px;
  display: flex;
  transform: translateY(320px);
  flex-direction: column;
  justify-content: center;
  transition: all 500ms;
}

.glHeader_nav_links a {
  position: relative;
  margin: 20px 10px 20px 0;
  padding: 0;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  color: #fff;
  border: 1px solid #fff;
}

.glHeader_nav_links .iStyle,
.glHeader_nav_links i {
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  margin: auto;
}

.glHeader_nav_links .iStyle {
  width: 20px;
  pointer-events: none;
}

.glHeader_nav_links i {
  font-size: 20px;
}

.glHeader_nav_links .i-mail {
  display: none;
}

.glHeader_nav_links .i-cart svg {
  fill: #fff;
}

.glHeader_inner {
  width: 100%;
  position: fixed;
  top: 0;
  height: 60px;
  background: #0a0a0a;
  box-shadow: 0px 16px 35px rgba(0, 0, 0, 0.5);
  z-index: 10;
}

.glHeader_inner_text {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  margin-left: 15px;
}

.glHeader_inner_text .titleName {
  font-family: "Montserrat", sans-serif;
  color: #fff;
  font-weight: 400;
  font-size: 22.65248px;
  margin: 0;
  line-height: 1em;
}

.glHeader_inner_text .titleName span {
  font-size: 8.65248px;
  margin-left: 8px;
  font-weight: 300;
}

.glHeader_inner_text div {
  display: none;
}

.glHeader_inner_text p {
  font-family: "Montserrat", sans-serif;
  color: #fff;
  margin: 0;
  font-size: 5.34752px;
}

.glHeader_inner_text p a {
  color: #fff;
  transition: all 300ms;
}

.glHeader_inner_text p a:hover {
  color: #cccccc;
}

.glHeader_cover {
  visibility: hidden;
  position: fixed;
  width: 100%;
  height: 800px;
  z-index: 15;
  transition: all 300ms;
  background: #0a0a0a;
  opacity: 0;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.glbg {
  width: 100%;
  min-height: 400px;
  position: relative;
  box-shadow: 0px 10px 35px rgba(0, 0, 0, 0.2);
